<template>
  <div style="overflow:hidden" id="wrap">
    <div id="top" @click="back()">
      <img src="../../assets/back.png" alt="">
      <p></p>
    </div>
    <div style="color:gray" id="tex1">
        20161010 313034 
    </div>
    <div id="tex2">
        <p class="tit">Q1: </p>
        <p style="color:gray;
        margin-top:0.15rem"></p>
        <p style="color:gray;margin-top:0.1rem"> 313034</p>
        <p style="color:gray"></p>
    </div>
    <div id="tex3">
         <p class="tit">Q2: </p>
         <p style="color:gray;margin-top:0.15rem">20</p>
    </div>
    <div id="tex4">
         <p class="tit">Q3: </p>
         <p style="color:gray;margin-top:0.15rem"></p>
         <div id="content">
             <p style="color:gray">31</p>
         <p style="color:gray">3130</p>
         </div>
    </div>
    <div id="tex5">
         <p class="tit">Q4: </p>
         <p style="color:gray;margin-top:0.15rem">App>></p>
    </div>
    <div id="tex6">
         <p class="tit">Q5: </p>
         <p style="color:gray;margin-top:0.15rem"></p>
         <p style="color:gray;margin-top:0.1rem"></p>
    </div>
  </div>
</template>
<script>
export default {
  name: "youhui",
  data() {
    return {};
  },
  methods: {
    back() {
      this.$router.back();
    }
  }
};
</script>
<style scoped>
#wrap {
  width: 3.75rem;
  position: relative;
}
#top {
  width: 100%;
  height: 0.55rem;
  background-color: rgb(49, 144, 232);
  position: fixed;
  z-index: 2;
  left: 0;
  top: 0;
}
#top img {
  height: 0.25rem;
  position: absolute;
  left: 5%;
  top: 50%;
  transform: translate(-50%, -50%);
}
#top p {
  font-size: 0.2rem;
  color: white;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
#tex1{
    margin-top: 0.65rem;
    width: 3rem;
    font-size: 0.15rem;
    margin-left: 0.375rem;
}
.tit{
    font-size: 0.18rem;
    font-weight: bold;
}
#tex2,#tex3,#tex4,#tex5,#tex6{
    width: 3.55rem;
    margin-left: 0.1rem;
    font-size: 0.15rem;
    margin-top: 0.12rem;
}
#tex2 p:nth-child(2),#tex2 p:nth-child(3),#tex2 p:nth-child(4){
    margin-top: 0.05rem;
}
#content{
     width: 3.1rem;
    margin-left: 0.54rem;
    font-size: 0.15rem;
    margin-top: 0.12rem;
}
</style>



